.box-section-title {
  text-decoration: none;
  color: #134761;
  transition: all .1s;

  &:hover {
    color: #f97413;
  }

  & h2 {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 30px;
    margin: 25px 0;
  }

}

.box-link {
  display: inline-block;
  text-decoration: none;
  margin: 0 45px 0 0;
}

.box {
  position: relative;
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  border-radius: 3px;
  background: #fff;
  transition: all .1s;
  box-shadow: 0 1px 3px rgba(0,0,0,.09),0 1px 2px rgba(0,0,0,.18);

  &:hover {
    background: #f9a143;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  }

  &__img {
    display: block;
    width: 100%;
    height: auto;
    border-radius: 3px 3px 0 0;
  }

  &__title {
    position: relative;
    font-size: 18px;
    line-height: 24px;
    color: #666;
    margin: 0;
    padding: 20px;
    z-index: 2;
    transition: all .1s;
  }

  &:hover &__title {
    color: #000;
  }

  &__date {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    color: #666;
    margin: 0;
    padding: 0 20px 20px 20px;
    z-index: 2;
    transition: all .1s;
  }

  &:hover &__date {
    color: #000;
  }


  &__sub {
    width: 100%;
    height: 100%;
    background: #f9a143;
    position: absolute;
    border-radius: 3px;
    top: 0;
    opacity: 0;
    transition: all .1s;
  }

  &:hover &__sub {
    opacity: 0.6;
  }

}
