.search-wrap {
  width: 100%;
  //height: 500px;
  background: url("../images/bg.jpg") no-repeat;
  background-position: center;
  background-size: 100% auto;

  &__wt {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.86);
  }

}

.search {

  &__box {
    max-width: 700px;
    margin: 0 auto;
    padding: 100px 0;
    text-align: center;
    font-family: 'Roboto', sans-serif;
  }

  &__title {
    margin: 0;
    padding-bottom: 30px;
    font-size: 34px;
    font-weight: 400;
    color: #134761;
  }

  &__nav {
    font-size: 18px;
    font-weight: 400;
    padding: 30px 20px;

    & a {
      color: #134761;
      padding-right: 25px;
      transition: all .1s;

      &:last-child {
        padding-right: 0;
      }

      &:hover {
        color: #f97413;
      }

    }

  }

}

@media only screen and (max-width : 992px) {
  .search-wrap {
    width: 100%;
    background: #f5f5f5;

    &__wt {
      background: none;
    }

  }

  .search {

    &__box {
      padding: 45px 0 0 0;
    }

    &__nav {
      padding-bottom: 0;

      & a {
        display: block;
        margin-bottom: 25px;

        &:last-child {
          padding-bottom: 0;
        }

      }

    }


  }

}

