@import 'https://fonts.googleapis.com/css?family=Roboto:400,500,700&subset=cyrillic';
@import "normalize";

* {
  box-sizing: border-box;
}

body, html {
  width: 100%;
  height: 100%;
}

.mask-content {
  display: none;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);

  &_open {
    transition: all .3s ease-out;
    display: block;
  }

}

