.header {
  position: relative;
  float: left;
  width: 280px;
  background: #134761;
  min-height: 100%;
}

.header-logo {
  display: inline-block;
  margin: 25px;
  text-decoration: none;

  &__brand {
    font: 0/0 serif;
    overflow: hidden;
    text-shadow: none;
  }

}

.header-menu-nav {
  &:after {
    content: "";
    display: table;
    clear: both;
  }

  &_open {
    transform: translateX(280px);
  }

  &__btn {
    display: none;
    float: right;
    padding: 15px;
    margin: 20px;
    border: 0;
    background: #f9a143;
    border-radius: 3px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 400;
    cursor: pointer;
    transition: all .1s;
    outline: 0;
    text-decoration: none;
    color: #000;

    &:hover {
      background: #f97413;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    }

    &:active {
      box-shadow: none;
    }
  }

}

.header-menu {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 20px;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  &__item {
    display: block;
    padding: 12px 25px 15px 25px;
    text-decoration: none;
    color: #fff;
    border-bottom: 1px solid #426c81;
    transition: all .1s;

    &_active {
      color: #000000;
      background: #f9a143;
      border-bottom-color: #f9a143;
    }

    &:hover {
      color: #000000;
      background: #f9a143;
    }

    &:active {
      background: #f97413;
    }

  }

}

.header-nav-wrap {
  background: #ececec;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

}

.header-page-nav {
  display: inline-block;
  float: left;

  &_fr {
    float: right;
  }

  &:after {
    content: "";
    display: table;
    clear: both;
  }

}

.header-page-settings {
  display: inline-block;
  float: left;
  margin: 0;
  padding: 15px 0 15px 25px;
  list-style: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  & li {
    display: inline-block;
    float: left;
    margin: 0 1px 0 0;
    padding: 0;
    background: #fff;
    transition: all .1s;

    &:hover {
      background: #f97413;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    }

    &:active {
      box-shadow: none;
    }

    &:first-child {
      border-radius: 3px 0 0 3px;
    }

    &:last-child {
      border-radius: 0 3px 3px 0;
    }

  }

  & a {
    display: inline-block;
    padding: 8px 15px;
    text-decoration: none;
    font-size: 14px;
    color: #424242;
    transition: all .1s;
  }

}

.header-page-menu {
  display: inline-block;
  float: left;
  margin: 0;
  padding: 20px;
  list-style: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  & li {
    display: inline-block;
    float: left;
    margin: 0 1px 0 0;
    padding-right: 40px;
    transition: all .1s;

    &:last-child {
      padding-right: 0;
    }

  }

  & a {
    text-decoration: none;
    font-size: 18px;
    color: #134761;
    transition: all .1s;

    &:hover {
      color: #f97413;
    }

  }
}

.header-login-btn {
  display: inline-block;
  margin: 15px 25px;
  padding: 5px 20px;
  border: 0;
  background: #f9a143;
  border-radius: 3px;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
  transition: all .1s;
  outline: 0;
  text-decoration: none;
  color: #000;

  &:hover {
    background: #f97413;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  }

  &:active {
    box-shadow: none;
  }
}


@media only screen and (max-width : 992px) {
  .header {
    display: block;
    width:100%;
    min-height: 0;
  }

  .header-logo {

    & img {
      width: 150px;
    }

  }

  .header-menu-nav {
    display: block;
    position: absolute;
    background: #134761;
    top: 0;
    min-height: 100%;
    width: 280px;
    z-index: 8;
    left: -280px;
    overflow: auto;

    &__btn {
      display: inline-block;
    }

  }

  .header-nav-wrap {
    text-align: right;
  }

  .header-page-settings {
    display: none;
  }

}

