.rudi-search-wrap {
  padding: 0 20px;
}

.rudi-search {
  //padding: 20px 10px;
  border-radius: 3px;

  &-link {
    position: relative;
    display: inline-block;
    text-decoration: none;
    font-size: 0.875em;
    font-weight: 300;
    color: #00cc99;
    margin: 5px 10px 5px 0;

    &:hover {
      color: #00cc99;
    }
  }

  &-checkbox {
    position: relative;
    display: inline-block;
    color: #444;
    font-size: 0.875em;
    font-weight: 300;
    margin: 5px 10px 5px 0;

    & label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
    }

    & input[type=checkbox] {
      position: absolute;
      margin-left: -20px;
    }
  }

  &-radio {
    position: relative;
    display: inline-block;
    color: #444;
    font-size: 0.875em;
    font-weight: 300;
    margin: 5px 10px 5px 0;;

    & label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
    }

    & input[type=radio] {
      position: absolute;
      margin-left: -20px;
    }
  }
}

.rudi-search-input-form-group {
  display: table-cell;
  vertical-align: middle;
  width: 1%;
}

.rudi-search-form {
  position: relative;
  display: table;
  width: 100%;
  border-radius: 3px;
  border-collapse: separate;

  &__select {
    display: table-cell;
    height: 34px;
    outline: 0;
    cursor: pointer;
    background: #fff;
    border: 2px #f9a143 solid;
    border-right: solid 1px #ededed;
    //-moz-appearance: none;
    //-webkit-appearance: none;
    //appearance: none;
    //background: url('../images/selectdown.svg') no-repeat 95% 50%;
    //background-size: 13px;
    padding: 5px 25px 5px 5px;
    border-radius: 3px 0 0 3px;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 400;
    color: #343434;
    transition: all 0.2s;

    &:focus {
      border-color: #f97413;
    }
  }

  &__input {
    display: table-cell;
    position: relative;
    float: left;
    width: 100%;
    //height: 34px;
    padding: 13px;
    vertical-align: middle;
    outline: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 400;
    color: #343434;
    border: 2px #f9a143 solid;
    border-left-color: #fff;
    border-right-color: #fff;
    background: #fff;
    transition: all 0.2s;

    &:focus {
      border-color: #f97413;
    }

    &_main-page {
      border: 2px #f9a143 solid;
      border-radius: 3px 0 0 3px;
    }

  }

  &__submit {
    display: table-cell;
    position: relative;
    padding: 15px 20px;
    //height: 34px;
    background-color: #f9a143;
    color: #000;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 400;
    border: 0;
    border-radius: 0 3px 3px 0;
    cursor: pointer;
    outline: none;
    transition: all 0.2s;

    &:hover {
      background-color: #f9a143;
    }
    &:active {
      background-color: #f97413;
    }

  }

}
